import request from '@/static/request.js'
export default {
  data() {
    return {
      id: 0,
      events: [],
    };
  },
  mounted() {
    const vm = this
    request
      .get('Activity/', {
		
      })
      .then(res => {
        vm.events = res.data
      })
  },
  onLoad(options) {
    uni.getStorage({
      key: 'GetId',
      success: res => {
        let self = this;
        self.userId = res.data;
      }
    })
  },
  methods: {
    toEvent(a) {
      uni.setStorage({
        key: 'ActivityId',
        data: a
      })
      uni.navigateTo({
        url: '../schoolmates/event'
      });
    },
    toDataBank() {
      uni.navigateTo({
        url: '../schoolmates/dataBank'
      });
    },
    ifPassed(time) {
      const vm = this
	  let endTime = new Date(Date.parse(time.split(',')[1]))
	  let now = new Date()
	  if(endTime<now)
	  {
		  return true
	  } else {
		  return false
	  }
    },
  },
};
